
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import { mapState } from 'vuex'
import ListCell from '../../crm/components/ListCell.vue'
import { AjaxHttp } from '../../../js/ajaxHttp'

export default {
name: 'my',
components: {
Group,
Cell,
ListCell,
Grid,
GridItem,
},
computed: {
...mapState({
userInfo: state => state.userInfo
}),
},
data() {
return {
tablist: [],
itemlist: [
/*[{
title: '我的动态',
path: 'list/' + 'default',
url: require('../../../assets/my_dy.png')
}, {
title: '我的代办',
path: 'list/' + 'default',
url: require('../../../assets/my_do.png')
}],*/
[
/*{
title: '邀请同事',
path: 'list/' + 'default',
url: require('../../../assets/my_invite.png')
}, */
{
title: '设置',
path: '/set',
url: require('../../../assets/my_set.png')
}
]
]
}
},
mounted() {
if (!window.localStorage.wkurl) {
this.$router.replace({
path: '/welcome/login'
})
}
if (this.userInfo.address_book) {
this.tablist.push({
title: '通讯录',
path: '/addresslist',
url: require('../../../assets/my_adb.png')
});
}

this.tablist.push({
title: '日志',
path: 'loglist',
url: require('../../../assets/my_log.png')
});
if (this.userInfo.task) {
this.tablist.push({
title: '任务',
path: 'project/default',
url: require('../../../assets/my_task.png')
});
}
if (this.userInfo.event) {
this.tablist.push({
title: '日程',
path: 'schedule',
url: require('../../../assets/my_schedule.png')
});
}
},
methods: {
enterPersonInfo() {
this.$router.push({
name: 'user',
params: {
id: this.userInfo.role_id
}
})
}
}
}




